@charset "UTF-8";
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
}

body {
  font-family: "Inter", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  background-color: #111111;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.material-symbols-outlined {
  vertical-align: middle;
  line-height: 1;
}

.container {
  max-width: 1440px;
  height: 100%;
  margin: 0 auto;
  padding: 0 8px;
}
.container--small {
  max-width: 992px;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  white-space: nowrap !important;
}

[hidden] {
  display: none !important;
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(16, 185, 129, 0.3) transparent;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 3px;
}

*::-webkit-scrollbar-thumb {
  background: rgba(16, 185, 129, 0.3);
  border-radius: 3px;
  transition: background 0.2s;
}
*::-webkit-scrollbar-thumb:hover {
  background: rgba(16, 185, 129, 0.5);
}
*::-webkit-scrollbar-thumb:active {
  background: #10B981;
}

.scrollbar-thin, .hero-split__matches, .secondary-nav__list {
  scrollbar-width: thin;
}
.scrollbar-thin::-webkit-scrollbar, .hero-split__matches::-webkit-scrollbar, .secondary-nav__list::-webkit-scrollbar {
  height: 3px;
}

.scrollbar-hidden {
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}

.scrollbar-accent {
  scrollbar-color: rgba(245, 158, 11, 0.3) transparent;
}
.scrollbar-accent::-webkit-scrollbar-thumb {
  background: rgba(245, 158, 11, 0.3);
}
.scrollbar-accent::-webkit-scrollbar-thumb:hover {
  background: rgba(245, 158, 11, 0.5);
}

.header {
  background: #0C0C0C;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
  display: flex;
  align-items: center;
  gap: 32px;
  height: 64px;
}

.header__logo {
  display: flex;
  align-items: center;
  gap: 4px;
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-decoration: none;
  transition: opacity 0.2s;
}
.header__logo:hover {
  opacity: 0.8;
}

.header__logo-text {
  color: #FFFFFF;
}

.header__logo-accent {
  color: #10B981;
}

.header__main-nav {
  flex: 1;
}

.main-nav__list {
  display: flex;
  align-items: center;
  gap: 8px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__item {
  position: relative;
}
.main-nav__item:last-child {
  margin-left: auto;
}
.main-nav__item:last-child .main-nav__dropdown {
  right: 0;
  left: auto;
}
.main-nav__item--active .main-nav__link {
  background: #1A1A1A;
  color: #10B981;
  border-bottom: 2px solid #10B981;
}

.main-nav__link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: #9CA3AF;
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
  height: 64px;
}
.main-nav__link:hover {
  background: #1A1A1A;
  color: #FFFFFF;
}
.main-nav__link--dropdown[aria-expanded=true] {
  background: #1A1A1A;
  color: #10B981;
}
.main-nav__link--dropdown[aria-expanded=true] .main-nav__arrow {
  transform: rotate(180deg);
}

.main-nav__icon {
  font-size: 20px;
  line-height: 1;
}
.main-nav__icon.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 20;
}

.main-nav__text {
  white-space: nowrap;
}

.main-nav__arrow {
  transition: transform 0.2s;
  font-size: 20px;
}
.main-nav__arrow.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 20;
}

.main-nav__dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 220px;
  background: #0C0C0C;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
  margin-top: 4px;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-8px);
  transition: opacity 0.2s, transform 0.2s;
  pointer-events: none;
}
.main-nav__dropdown:not([hidden]) {
  opacity: 1;
  transform: translateY(0);
  z-index: 1000;
  pointer-events: auto;
}

.main-nav__dropdown-list {
  list-style: none;
  margin: 0;
  padding: 8px;
}

.main-nav__dropdown-item {
  margin: 0;
  position: relative;
}

.main-nav__dropdown-divider {
  height: 1px;
  background: rgba(255, 255, 255, 0.1);
  margin: 8px 0;
  list-style: none;
}

.main-nav__dropdown-link {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 14px;
  border-radius: 6px;
  border: none;
  background: transparent;
  width: 100%;
  cursor: pointer;
  transition: all 0.2s;
}
.main-nav__dropdown-link:hover {
  background: rgba(16, 185, 129, 0.1);
  color: #10B981;
}
.main-nav__dropdown-link--submenu {
  justify-content: space-between;
}
.main-nav__dropdown-link--submenu:hover .main-nav__dropdown-arrow {
  transform: translateX(4px);
}
.main-nav__dropdown-link .main-nav__icon {
  font-size: 16px;
}

.main-nav__dropdown-arrow {
  font-size: 18px;
  transition: transform 0.2s;
}

.header__burger {
  display: none;
  flex-direction: column;
  justify-content: center;
  margin-left: auto;
  align-items: center;
  gap: 5px;
  width: 40px;
  height: 40px;
  background: transparent;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  padding: 0;
  transition: background 0.2s;
}
.header__burger:hover {
  background: #1A1A1A;
}
.header__burger[aria-expanded=true] .header__burger-line:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}
.header__burger[aria-expanded=true] .header__burger-line:nth-child(2) {
  opacity: 0;
}
.header__burger[aria-expanded=true] .header__burger-line:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

.header__burger-line {
  width: 24px;
  height: 2px;
  background: #FFFFFF;
  border-radius: 2px;
  transition: all 0.3s;
}

.header__secondary-nav {
  background: #0C0C0C;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;
}

.secondary-nav__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
  position: relative;
}

.secondary-nav__container::before,
.secondary-nav__container::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 40px;
  pointer-events: none;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.3s;
}

.secondary-nav__container::before {
  left: 0;
  background: linear-gradient(90deg, #0C0C0C, transparent);
}

.secondary-nav__container::after {
  right: 0;
  background: linear-gradient(-90deg, #0C0C0C, transparent);
}

.secondary-nav__container.has-scroll-left::before {
  opacity: 1;
}

.secondary-nav__container.has-scroll-right::after {
  opacity: 1;
}

.secondary-nav__list {
  display: flex;
  align-items: center;
  gap: 4px;
  list-style: none;
  margin: 0;
  padding: 0;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  -webkit-mask-image: linear-gradient(90deg, transparent, black 20px, black calc(100% - 20px), transparent);
  mask-image: linear-gradient(90deg, transparent, black 20px, black calc(100% - 20px), transparent);
}

.secondary-nav__item--active .secondary-nav__link {
  background: rgba(16, 185, 129, 0.1);
  color: #10B981;
  border-bottom: 2px solid #10B981;
}

.secondary-nav__link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  color: #9CA3AF;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  border-bottom: 2px solid transparent;
  transition: all 0.2s;
}
.secondary-nav__link:hover {
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.05);
}

.secondary-nav__icon {
  font-size: 20px;
  line-height: 1;
}
.secondary-nav__icon.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 20;
}

@media (max-width: 1024px) {
  .header__container {
    gap: 16px;
    padding: 0 16px;
  }
  .main-nav__text {
    font-size: 12px;
  }
  .main-nav__link {
    padding: 12px 12px;
    gap: 6px;
  }
  .secondary-nav__container {
    padding: 0 16px;
  }
}
@media (max-width: 768px) {
  .header__container {
    height: 56px;
    padding: 0 16px;
  }
  .header__logo {
    font-size: 16px;
  }
  .header__burger {
    display: flex;
  }
  .header__main-nav {
    position: fixed;
    top: 56px;
    left: 0;
    right: 0;
    bottom: 0;
    background: #0C0C0C;
    padding: 16px;
    transform: translateX(-100%);
    transition: transform 0.3s;
    overflow-y: auto;
    z-index: 999;
  }
  .header__main-nav.is-open {
    transform: translateX(0);
  }
  .main-nav__list {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  .main-nav__item {
    width: 100%;
  }
  .main-nav__link {
    width: 100%;
    justify-content: flex-start;
    padding: 16px;
    height: auto;
    border-bottom: none;
    border-radius: 8px;
  }
  .main-nav__link:hover {
    background: #1A1A1A;
  }
  .main-nav__text {
    display: block;
    font-size: 14px;
  }
  .main-nav__icon {
    font-size: 24px;
  }
  .main-nav__dropdown {
    position: static;
    background: transparent;
    border: none;
    box-shadow: none;
    margin-top: 8px;
    padding-left: 20px;
  }
  .main-nav__dropdown-list {
    padding: 0;
  }
  .main-nav__dropdown-link {
    padding: 12px 16px;
    font-size: 14px;
  }
  .main-nav__dropdown-link--submenu {
    background: rgba(255, 255, 255, 0.05);
    margin-bottom: 4px;
  }
  .main-nav__submenu {
    position: static;
    background: transparent;
    border: none;
    box-shadow: none;
    margin-top: 4px;
    padding-left: 20px;
    max-height: none;
    transform: none;
  }
  .main-nav__submenu-list {
    padding: 0;
  }
  .main-nav__submenu-link {
    padding: 10px 16px;
    font-size: 13px;
    background: rgba(255, 255, 255, 0.03);
    margin-bottom: 2px;
  }
  .secondary-nav__container {
    padding: 0 12px;
  }
  .secondary-nav__list {
    gap: 0;
  }
  .secondary-nav__link {
    padding: 12px 16px;
    flex-direction: column;
    gap: 4px;
    font-size: 11px;
  }
  .secondary-nav__icon {
    font-size: 20px;
  }
  .secondary-nav__text {
    display: block;
  }
}
@media (max-width: 480px) {
  .header__container {
    padding: 0 12px;
  }
  .header__logo {
    font-size: 14px;
  }
  .secondary-nav__link {
    padding: 10px 12px;
    font-size: 10px;
  }
  .secondary-nav__icon {
    font-size: 18px;
  }
}
.section__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.section--margin {
  margin-bottom: 64px;
}
.section__inner {
  position: relative;
  z-index: 2;
}

.news__layout {
  display: grid;
  grid-template-columns: 1fr 350px;
  gap: 32px;
  margin-top: 32px;
}

.news__main {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.news__sidebar {
  display: flex;
  flex-direction: column;
  gap: 16px;
  position: sticky;
  top: 148px;
  align-self: flex-start;
}

.news__sidebar-title {
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 20px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0 0 8px;
  padding-bottom: 12px;
  border-bottom: 2px solid #10B981;
}

@media (max-width: 1024px) {
  .news__layout {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  .news__sidebar {
    position: static;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  .news__sidebar-title {
    grid-column: 1/-1;
  }
}
@media (max-width: 768px) {
  .news__layout {
    gap: 32px;
    margin-top: 24px;
  }
  .news__sidebar {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}
@media (max-width: 480px) {
  .news__layout {
    margin-top: 20px;
  }
}
.archive__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 24px;
  margin-bottom: 40px;
}
@media (max-width: 768px) {
  .archive__grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}

.archive__grid.archive__grid--lives {
  grid-template-columns: repeat(auto-fill, minmax(600px, 1fr));
}

.archive__grid.archive__grid--news {
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
}
.archive__grid.archive__grid--news .card-post__title {
  font-size: 20px;
  line-height: 1.2;
  margin-bottom: 8px;
}
.archive__grid.archive__grid--news .card-post__meta {
  font-size: 16px;
}

.hero-split {
  padding: 32px 0 0;
}

.hero-split__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 32px;
  min-height: 600px;
}

.hero-split__featured {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  background: #1A1A1A;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.hero-split__featured-link {
  display: block;
  position: relative;
  height: 100%;
  text-decoration: none;
  transition: transform 0.3s;
}
.hero-split__featured-link:hover .hero-split__image,
.hero-split__featured-link:hover .hero-split__video {
  transform: scale(1.05);
}
.hero-split__featured-link:hover .hero-split__read-more {
  transform: translateX(8px);
}

.hero-split__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.hero-split__image,
.hero-split__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.hero-split__image--placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
}
.hero-split__image--placeholder .material-symbols-outlined {
  font-size: 120px;
  color: #9CA3AF;
  opacity: 0.3;
  font-variation-settings: "FILL" 0, "wght" 200, "GRAD" 0, "opsz" 48;
}

.hero-split__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(12, 12, 12, 0.95) 0%, rgba(12, 12, 12, 0.7) 40%, rgba(12, 12, 12, 0.3) 70%, transparent 100%);
}

.hero-split__content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 16px;
  padding: 40px;
  height: 100%;
}

.hero-split__badge {
  display: inline-flex;
  align-items: center;
  padding: 8px 16px;
  background: #10B981;
  color: #0C0C0C;
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 6px;
  width: fit-content;
}

.hero-split__title {
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 48px;
  font-weight: 800;
  color: #FFFFFF;
  line-height: 1.1;
  margin: 0;
  text-shadow: 0 2px 8px rgba(12, 12, 12, 0.5);
}

.hero-split__description {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
  margin: 0;
  max-width: 600px;
}

.hero-split__meta {
  display: flex;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.hero-split__sport {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  font-weight: 600;
  color: #10B981;
}
.hero-split__sport .material-symbols-outlined {
  font-size: 18px;
  font-variation-settings: "FILL" 0, "wght" 500, "GRAD" 0, "opsz" 18;
}

.hero-split__date {
  font-size: 14px;
  color: #9CA3AF;
}

.hero-split__action {
  margin-top: 8px;
}

.hero-split__read-more {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 14px 32px;
  background: #10B981;
  color: #0C0C0C;
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  border-radius: 8px;
  transition: all 0.3s;
}
.hero-split__read-more .material-symbols-outlined {
  font-size: 20px;
  transition: transform 0.3s;
}

.hero-split__sidebar {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.hero-split__sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  padding-bottom: 16px;
  border-bottom: 2px solid #10B981;
}

.hero-split__sidebar-title {
  display: flex;
  align-items: center;
  gap: 12px;
  font-family: "Montserrat", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
}
.hero-split__sidebar-title .material-symbols-outlined {
  font-size: 28px;
  color: #10B981;
  font-variation-settings: "FILL" 0, "wght" 500, "GRAD" 0, "opsz" 28;
}

.hero-split__sidebar-link {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  font-size: 14px;
  font-weight: 600;
  color: #10B981;
  text-decoration: none;
  white-space: nowrap;
  transition: all 0.2s;
