.table__wrap.is-scrollable::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 40px;
  background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.4));
  pointer-events: none;
  z-index: 2;
  transition: opacity 0.3s ease;
}
