  opacity: 0;
}
.table__wrap.table__wrap--scrolled::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 40px;
  background: linear-gradient(to left, transparent, rgba(0, 0, 0, 0.3));
